import { Component,ViewChild } from '@angular/core';
import { ViewController,AlertController,IonicPage,  NavController,Events , ActionSheetController, NavParams } from 'ionic-angular';
import {  FormControl,  FormBuilder,  FormGroup,   Validators } from '@angular/forms';
import { Auth,NotificationProvider } from '../../../providers';
import { TranslateService } from '@ngx-translate/core';
import { Storage } from '@ionic/storage';
import { IonicSelectableComponent } from 'ionic-selectable';
import { Form } from '../../../types';

@IonicPage()
@Component({
  selector: 'page-formsd-create',
  templateUrl: 'formsd-create.html',
})
export class FormsdCreatePage {
  @ViewChild('portComponent') portComponent: IonicSelectableComponent;
  public form                  : FormGroup;
  username: string = "";
  public nidformulario                 : any;
  public idformulario                 : any;
  public name                 : any;
  public order                 : any;
  public title                 : any;
  public type		     : any;
  public estado		     : any;
  idafiliado:any='na';
  vcategoria: any=[];
  desabilita:boolean=false;
  noorden:any;
  private _ID                  : String;
  idempresa:any;
  public pageTitle           	: string;
  createdCode = null;
  scannedCode = null;

  isDisablednit :any;
  constructor(public alertCtrl: AlertController,public storage: Storage,  public msj: NotificationProvider, public translateService: TranslateService,public viewCtrl: ViewController,public authService:  Auth,public navCtrl 		: NavController,  
    public navParams 	: NavParams,   private _FB          : FormBuilder,         public actionSheetCtrl: ActionSheetController, 
          public events: Events)
  {
     // Define form validation - VERY basic!
     this.form = this._FB.group({
        name     :  ['', Validators.required],
        order    :  ['', Validators.required],
        title     : ['', Validators.required],
        type     : ['', Validators.required],
        estado    : ['', Validators.required]
      
     });

     this.translateService.get('APPX').subscribe((value) => {
      this.idempresa=value
      this.authService.getReg('todos/type-formmovild/'+ this.idempresa,'frmcats').then((data) => {
        this.vcategoria=data;
       });
  
      
  })

    


  }
  ngOnInit() {

  



    this.storage.get('username').then((value) => {
      this.username = value;
  }); 

  this.portNameControl = this._FB.control(null, Validators.required);
 
  this.portForm = this._FB.group({
    portName: this.portNameControl
  
  });

  }

  ionViewDidLoad() : void
  {this.idformulario=this.navParams.data.record2.idformulario;
    this.nidformulario=this.navParams.data.record2.nidformulario;

    if(this.navParams.get("record")==null)
    {
        this.noorden=0;
        this.order=10;

        this.authService.getReg(this.idformulario + '/tipofrmorden' ,'frmmovilds').then((data) => {
        
          if(data)
          {
            var aa:any=data;
            this.noorden=aa.orden;
            this.order=aa.orden;
            this.order=this.order+10;
          }
          
        
        }, (err) => {
          this.msj.showError3('Error',err.error);
        // this.translateService.get('NO_OPERACION').subscribe((value) => { this.msj.showError3('Error',value);  })  
        });
        
    }

    if(this.navParams.get("record")==null)
    {
      this.desabilita=false; 
      this.translateService.get('ITEM_CREATE_TITLE').subscribe((value) => {
        this.pageTitle=value;
      });
      
      this.isDisablednit =true
    }
    else {
      this.desabilita=true;
       this._ID 				=	this.navParams.data.record._id;
       this.name		=	this.navParams.data.record.name;
       this.order		=	this.navParams.data.record.order;
       this.title		=	this.navParams.data.record.title;
       this.type		=	this.navParams.data.record.type;
       this.estado		=	this.navParams.data.record.estado;
       this.isDisablednit =false
       this.translateService.get('ITEM_UPDATE_TITLE').subscribe((value) => {
        this.pageTitle=value;
      });
    

    }
  }


  manageGallery() : void
  {
   
  let creat='Actualiza';
    if(this.navParams.get("record")==null)
    {  creat='Crea';
    }

     let   options       : any	     = { 
       idempresa:this.idempresa,
      idmovil : this.idformulario,
     name : this.form.controls['name'].value,
          order : this.form.controls['order'].value,
     title : this.form.controls['title'].value,
     type : this.form.controls['type'].value.id,
     estado : this.form.controls['estado'].value,
     bitacora:{idempresa:this.idempresa,idafiliado:'',email: this.username,permiso:creat,accion:creat+' campo de formulario movil '}
    };


     if(this.navParams.get("record")==null)
     {  
          if(options){
            this.authService.createRegmovil('',options,'frmmovilds').then((result) => {
              if (!this.form.valid) { return; }
              console.log(result)
              this.viewCtrl.dismiss(result);


          }, (err) => {  
            this.msj.showError3('Error',err.error);
          });
          }
     }   
     else
     {
      if(options){
        this.authService.createRegmovil(this._ID ,options,'frmmovilds').then((result) => {
         
          if (!this.form.valid) { return; }
          console.log(result)
          this.viewCtrl.dismiss(result);
       }, (err) => {
        this.msj.showError3('Error',err.error);
       });
    }}
  }

  cancel() {
    this.viewCtrl.dismiss();
  }
  done() {
    if (!this.form.valid) { return; }
    //this.viewCtrl.dismiss(this.form.value);
  }



//********************************************************************************************* */
port: Form;
portForm: FormGroup;
portNameControl: FormControl;
  onAddForm(event: {
    component: IonicSelectableComponent
  }) {
    this.portNameControl.reset();
    this.portNameControl.setValue(event.component.searchText);
    event.component.showAddItemTemplate();
  }
  
  onSaveForm(event: {
    component: IonicSelectableComponent,
    item: Form
  }) {
    this.portNameControl.setValue(event.item.nombre);
    event.component.showAddItemTemplate();
  }
  



  deleteSelectedItem(op) {
    let itemToDeleteIndex;

    this.vcategoria.forEach((selectedItem, itemIndex) => {
      if (selectedItem._id==op) {
        itemToDeleteIndex = itemIndex;
      }
    });

    this.vcategoria.splice(itemToDeleteIndex, 1);
  }

  onDeleteForm(event: {
    component: IonicSelectableComponent,
    item: Form
  }) {
    let alert = this.alertCtrl.create({ title: 'Seguro de realizar esta operación?',
    buttons: [
      {text: 'Si',
        handler: () => {
          var aa:any;
          aa=event.item
          let recordID 		: string		= aa._id;
          this.authService.deleteReg(recordID+'/'+this.username+ '/' + this.idempresa + '/' + this.idafiliado,'frmcats').then((data) => {
            event.component.deleteItem({id:recordID,nombre:'aaa'});
            this.deleteSelectedItem(recordID);
              
          }, (err) => {
            this.msj.showError3('Error',err.error);
          });
      
        }
      },
      {
        text: 'No',  handler: () => { }
      }
    ]
  });
  alert.present();
  }
  
  addForm(op) {
    let creat='Crea';
     let   options       : any	     = { 
       idempresa: this.idempresa,
     idunidad : op,
     nombre : this.portNameControl.value,
     bitacora:{idempresa:this.idempresa,idafiliado:'',email: this.username,permiso:creat,accion:creat+' formulario movil '}
    };
    if(options){
      this.authService.createReg('',options,'frmcats').then((result) => {

        var aa:any=result;
        let port = new Form({
          id: aa._id,
          nombre: this.portNameControl.value
       
        });
        this.portComponent.addItem(port).then(() => {
          this.portComponent.search(port.nombre);
        });
        this.portNameControl.reset();
        this.portComponent.hideAddItemTemplate();
    }, (err) => {  
      this.msj.showError3('Error',err.error);
    });
    }



  }
  
  saveForm(port: Form,op) {
    let creat='Actualiza';
    let   options       : any	     = { 
      idempresa: this.idempresa,
    idunidad : op,
    nombre : this.portNameControl.value,
    bitacora:{idempresa:this.idempresa,idafiliado:'',email: this.username,permiso:creat,accion:creat+' formulario movil '}
   };
   if(options){
     var aa:any;
     aa=port;
     this.authService.createReg(aa._id,options,'frmcats').then((result) => {
       port.nombre = this.portNameControl.value;
       this.portComponent.hideAddItemTemplate();
   }, (err) => {  
     this.msj.showError3('Error',err.error);
   });
  }
}
  
  

moduloRecord(item : any) : void
{

   this.navCtrl.push('FormpropCreatePage', { record : item ,record2:{idformulario:this.idformulario}} );
}


}



